@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap');

html, body, div, span, applet, object, iframe,
h1, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, font, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td {
	margin: 0;
	padding: 0;
	border: 0;
	outline: 0;
	font-weight: inherit;
	font-style: inherit;
	font-size: 100%;
	font-family: inherit;
	vertical-align: baseline;
}


.weather-title{
    font-family: 'Roboto', sans-serif;
    background: #4988D8;;
    height: 100%;
    color: white;
    font-size: 34px;
    padding: 15px;
}

.content-container{
    margin-top: 150px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-self: center;
    gap: 120px;
    font-family: 'Roboto', sans-serif;
}


.weather-input{
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 20px;
}

.weather-input input{
    padding: 10px 15px;
    border-radius: 5px;
    border: 1px solid grey;
}

.weather-input label{
    margin-bottom: 20px;
}

.forecast-container {
    margin-top: 20px;
  }
  
  .forecast-container h3 {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .forecast-cards {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
  
  .forecast-card {
    border: 1px solid #4988D8;
    background: #4988D8;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    padding: 10px;
    width: 200px;
  }
  
  @media (max-width: 1279px) {
    .forecast-card {
      width: 100%;
    }
  }
  

.weather-card{
    border: 1px solid #4988D8;
    background: #4988D8;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    border-radius: 5px;
}

.weather-card h2{
    width: 100%;
}

.weather-card .weather-info{
    display: flex;
    gap: 20px;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.weather-card .weather-info .weather-humidity{
    width: 100%;
}

@media (max-width: 1279px){
    .weather-card {
        max-width: unset;
        margin: 0 20px;
    }

    .content-container{
        flex-direction: column;
        gap: 50px;
        margin-top: 70px;
    }
}